import emitter from 'element-ui/src/mixins/emitter'
import Migrating from 'element-ui/src/mixins/migrating'
import IconSave from '~/components/editor/assets/icons/save.svg?inline'

// 图片裁剪器配置参数
const _vueCropperOption = {
  autoCrop: false,
  autoCropHeight: 100,
  autoCropWidth: 100,
  canMove: true,
  canMoveBox: true,
  canScale: true,
  centerBox: false,
  enlarge: 1,
  fillColor: '',
  fixed: false,
  fixedBox: false,
  full: true,
  high: true,
  info: true,
  infoTrue: true,
  maxImgSize: 3000,
  mode: 'contain',
  original: false,
  outputSize: 1,
  outputType: 'webp',
  preW: 0
}
export default {
  name: 'EditorFormImgUploaderSimple',

  components: { IconSave },

  emit: ['upload-progress', 'upload-success', 'upload-error'],

  mixins: [emitter, Migrating],

  inject: {
    elForm: {
      default: ''
    },
    elFormItem: {
      default: ''
    }
  },

  inheritAttrs: false,

  props: {
    disabled: Boolean,
    readonly: Boolean,
    value: {
      type: Object,
      default () {
        return {
          originalSrc: '',
          src: '',
          width: '',
          height: ''
        }
      }
    }
  },

  data () {
    const propsValue = JSON.parse(JSON.stringify(this.value || {}))
    propsValue.outerWidth = propsValue.outerWidth === undefined ? -1 : propsValue.outerWidth
    propsValue.originalSrc = propsValue.originalSrc || propsValue.src

    return {
      nativeValue: propsValue,
      beforeCropperNativeValue: JSON.parse(JSON.stringify(propsValue)),
      progress: null,
      // 是否展开样式控件
      showCollapseName: '0',
      imgBaseUrl: process.env.imgBaseUrl || '',
      vueCropperOption: JSON.parse(JSON.stringify(_vueCropperOption)),
      vueCropperStyle: {},
      vueCropperImg: '',
      vueCropperCrap: false,
      uniqueId: null
    }
  },

  computed: {
    inputDisabled () {
      return this.disabled || (this.elForm || {}).disabled
    }
  },
  watch: {
    // 监听传入属性的改变，以实现v-model
    value: {
      deep: true,
      handler () {
        this.nativeValue = this.value
      }
    },
    // 监听本地数据的改变，以实现v-model
    nativeValue: {
      deep: true,
      handler (val) {
        this.$emit('input', val)
        this.dispatch('ElFormItem', 'el.form.change', [val])
      }
    }
  },
  mounted () {
    // 这里处理剪切的图片
    const propsValue = this.nativeValue
    if (propsValue.src.indexOf('blob:') === 0 && propsValue.originalSrc) {
      propsValue.src = propsValue.originalSrc
      this.handleChange()
    }
    this.vueCropperImg = this.imgBaseUrl + this.nativeValue?.src
  },
  methods: {
    // 备份属性值
    bakNativeValue () {
      this.beforeCropperNativeValue = JSON.parse(JSON.stringify(this.nativeValue))
    },
    // 还原属性值
    reduNativeValue () {
      this.nativeValue = JSON.parse(JSON.stringify(this.beforeCropperNativeValue))
    },
    /**
     * 在预览图片加载完成时计算图片尺寸
     * 适用于手动出入url的方式
     */
    handleImgLoaded (event) {
      this.nativeValue.width = event.target.naturalWidth
      this.nativeValue.height = event.target.naturalHeight
      this.bakNativeValue()
    },
    /**
     * 在选择文件上传时计算图片尺寸
     * 适用于上传录入方式
     * @param {*} uploadFile 上传的文件，仅在上传后返回
     * @param {*} uploadFileList 上传的文件列表，仅在上传后返回
     */
    handleUploadFileInputChange (uploadFile, uploadFileList) {
      if (uploadFile?.url) {
        const img = new Image()
        img.onload = () => {
          this.nativeValue.width = img.naturalWidth
          this.nativeValue.height = img.naturalHeight
          this.bakNativeValue()
        }
        img.src = uploadFile.url
      }
    },
    handleUploadProgress (event, file, fileList) {
      this.progress = Math.ceil(event.percent)
      this.$emit('upload-progress', event, file, fileList)
    },
    /**
     * 在上传成功/失败/手动编辑地址完成时触发
     * @param {*} uploadFile 上传的文件，仅在上传后返回
     * @param {*} uploadFileList 上传的文件列表，仅在上传后返回
     * @param {*} url 地址，在上传和手动修改文本框都返回
     */
    handleUploadChange (uploadFile, uploadFileList, url) {
      // 如果存在上传文件uploadFile并且其状态为success说明上传成功
      if (uploadFile?.status === 'success') {
        // 上传进度100%
        this.progress = 100
        this.$emit('upload-success', uploadFile, uploadFileList, url)
      }
      // 原始的图、裁剪之前
      this.nativeValue.originalSrc = url
      // PC用图、裁剪后
      this.nativeValue.src = url
      // 切图
      this.vueCropperImg = this.imgBaseUrl + url
      // 备份数据
      this.bakNativeValue()
      // 触发change事件
      this.handleChange()
    },
    handleUploadError (err, file, fileList) {
      this.progress = null
      this.$message({
        message: this.$t('upload file failed') + err.message,
        type: 'error'
      })
      this.$emit('upload-error', err, file, fileList)
    },
    handleChange (event) {
      this.$emit('change', this.nativeValue)
    },
    handleFocus (event) {
      this.$emit('focus', event)
    },
    handleBlur (event) {
      this.focused = false
      this.$emit('blur', event)
      if (this.validateEvent) {
        this.dispatch('ElFormItem', 'el.form.blur', [this.nativeValue])
      }
    },
    handleChangeCollapse (name) {
      // 如果点击的是当前面板，那么关闭面板
      this.showCollapseName = this.showCollapseName === name ? '' : name
      this.handleCollapseChanged(name)
    },
    handleCollapseChanged (name) {
      // 停止裁剪
      this.handleVueCropperStop()
      // 如果图片地址是blob:，那么要还原回
      if (this.nativeValue.src.indexOf('blob:') === 0) {
        this.reduNativeValue()
        this.$nextTick(() => {
          this.$emit('change', this.nativeValue)
        })
      }
    },
    handleVueCropperStart () {
      if (this.$refs.cropper) {
        this.vueCropperCrap = true
        this.$refs.cropper.startCrop()
      }
    },
    handleVueCropperStop () {
      if (this.$refs.cropper) {
        this.vueCropperCrap = false
        this.$refs.cropper.clearCrop()
      }
    },
    handleVueCropperRealTime (data) {
      if (!this.vueCropperCrap) {
        return
      }
      this.$refs.cropper.getCropBlob((data2) => {
        // do something
        this.nativeValue.src = URL.createObjectURL(data2)
        this.$emit('change', this.nativeValue)
        const img = new Image()
        img.onload = () => {
          this.nativeValue.width = img.width
          this.nativeValue.height = img.height
          URL.revokeObjectURL(img.src)
        }
        img.src = this.nativeValue.src
      })
    },
    handleVueCropperZoomIn (num) {
      this.$refs.cropper.changeScale(1)
    },
    handleVueCropperZoomOut (num) {
      this.$refs.cropper.changeScale(-1)
    },
    handleVueCropperRefresh () {
      this.$refs.cropper.refresh()
    },
    handleVueCropperRote () {
      this.$refs.cropper.rotateLeft()
    },
    handleVueCropperSave () {
      this.$refs.cropper.getCropBlob((blobData) => {
        // 使用内部的uploader子组件，再次完成上传工作
        blobData.name = 'cropper.webp'
        const uploadEl = this.$refs?.upload?.$refs?.upload
        const option = uploadEl?.$options?.propsData
        const httpRequest = uploadEl?.httpRequest
        if (uploadEl && option && typeof httpRequest === 'function') {
          option.file = blobData
          option.filename = option.name || 'file'
          httpRequest.apply(uploadEl, [option])
        }
        this.handleVueCropperStop()
      })
    },
    getVueCropperWidthAndHeight () {
      if (this.$refs.vueCropperWrapper && this.nativeValue?.width) {
        // const wrapperW = this.$refs.vueCropperWrapper.$el.clientWidth - 10
        // const rate = this.nativeValue.height / this.nativeValue.width
        return {
          height: 400 + 'px',
          with: '100%'
        }
      }
    },
    // 打开图片优化窗口 并传递参数
    handleOpenImgCompressed (imgUrl) {
      localStorage.setItem('imgCompressed', true)
      const e = 'https://squoosh.cloud-top.com.cn/'
      const t = 0.9 * window.innerWidth
      const r = 0.9 * window.innerHeight
      const a = (window.innerWidth, 0.01 * window.innerHeight)
      const squooshWin = window.open(
        e,
        '图像优化器',
        'width='
          .concat(t, ',height=')
          .concat(r, ',top=60,left=')
          .concat(a, ',popup')
      )
      localStorage.setItem('imgCompressed', false)
      this.imgToBase64(imgUrl).then((data) => {
        squooshWin.postMessage(data, '*')
        this.addEvent(squooshWin)
      })
    },
    // 图片转base64
    imgToBase64 (downloadUrl) {
      const _that = this
      const url = window.location.origin + '/file/' + downloadUrl
      return new Promise((resolve, reject) => {
        fetch(url).then(response => response.blob()).then((blob) => {
          const reader = new FileReader()
          reader.onload = function (e) {
            _that.uniqueId = _that.generateUniqueId()
            if (e?.target?.result) {
              resolve({
                type: 'fileChange',
                url: e.target.result,
                fileName: Date.now() + '.png',
                id: _that.uniqueId
              })
              console.log('e is', blob.type, e.target.result)
            }
          }
          reader.readAsDataURL(blob)
        })
      })
    },
    // 接收子窗口事件
    addEvent (squooshWin) {
      // 父级窗口接受‘imgCompressed’消息
      window.addEventListener('message', (e) => {
        console.log(e)
        if (e.data.type === 'imgCompressed' && e.data.id === this.uniqueId) {
          // const blobData = this.base64ToBlob(e.data.downloadUrl)
          // this.baseToFile(e.data.downloadUrl, 'aaa')
          fetch(e.data.downloadUrl).then(response => response.blob()).then((blobData) => {
            const uploadEl = this.$refs?.upload?.$refs?.upload
            const option = uploadEl?.$options?.propsData
            const httpRequest = uploadEl?.httpRequest
            if (uploadEl && option && typeof httpRequest === 'function') {
              option.file = blobData
              option.filename = option.name || 'file'
              httpRequest.apply(uploadEl, [option])
            }
            this.handleChange()
            squooshWin.close()
          })
        }
      })
    },
    generateUniqueId () {
      // 实现一个方法来生成唯一标识符
      return 'uniqueId-' + Math.random().toString(36).substr(2, 9)
    },
    base64ToBlob (base64, mime) {
      mime = mime || ''
      const sliceSize = 1024
      const byteChars = atob(base64)
      const byteArrays = []
      for (let offset = 0, len = byteChars.length; offset < len; offset += sliceSize) {
        const slice = byteChars.slice(offset, offset + sliceSize)
        const byteNumbers = new Array(slice.length)
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i)
        }
        const byteArray = new Uint8Array(byteNumbers)
        byteArrays.push(byteArray)
      }
      return new Blob(byteArrays, { type: mime })
    }
  }
}
